<template>
  <div id="app">
    <SideBar class="sidebar"></SideBar>

    <div class="content">
      <div class="table-caption">
        <caption>
          <h1>秘书表</h1>
        </caption>
        <div>
          <el-button type="primary" round @click="getSecretary"
            >获取秘书信息</el-button
          >
          <el-button type="warning" @click="dialogFormVisible = true"
            >添加秘书</el-button
          >
          <el-dialog title="添加秘书信息" :visible.sync="dialogFormVisible">
            <el-form :model="form">
              <el-form-item label="秘书号" :label-width="formLabelWidth">
                <el-input
                  v-model="form.secretaryNo"
                  autocomplete="off"
                ></el-input>
              </el-form-item>
              <el-form-item label="姓名" :label-width="formLabelWidth">
                <el-input v-model="form.name" autocomplete="off"></el-input>
              </el-form-item>
              <el-form-item label="性别" :label-width="formLabelWidth">
                <!-- <el-input
                    v-model.number="form.gender"
                    autocomplete="off"
                  ></el-input> -->
                <el-radio v-model="form.gender" label="男">男</el-radio>
                <el-radio v-model="form.gender" label="女">女</el-radio>
              </el-form-item>
              <el-form-item label="年龄" :label-width="formLabelWidth">
                <el-input
                  v-model.number="form.age"
                  autocomplete="off"
                ></el-input>
              </el-form-item>
              <el-form-item label="任用时间" :label-width="formLabelWidth">
                <!-- <el-input
                    v-model.number="form.hireDate"
                    autocomplete="off"
                  ></el-input> -->
                <el-date-picker
                  v-model="form.hireDate"
                  type="date"
                  placeholder="选择日期"
                >
                </el-date-picker>
              </el-form-item>
              <el-form-item label="职责" :label-width="formLabelWidth">
                <el-input
                  v-model.number="form.duty"
                  autocomplete="off"
                ></el-input>
              </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
              <el-button @click="dialogFormVisible = false">取 消</el-button>
              <el-button type="primary" @click="addSecretary">添 加</el-button
              ><!--dialogFormVisible = false-->
            </div>
          </el-dialog>
        </div>
      </div>

      <table class="table captain-top table-hover table-striped">
        <thead>
          <tr>
            <th scope="col">秘书号</th>
            <th scope="col">姓名</th>
            <th scope="col">性别</th>
            <th scope="col">年龄</th>
            <th scope="col">任用时间</th>
            <th scope="col">职责</th>
            <th scope="col">操作</th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <Secretary
            v-for="stu in secretary"
            :key="stu.secretaryNo"
            :secretary="stu"
          ></Secretary>
        </tbody>
      </table>
    </div>
  </div>
</template>
  
  <script>
import axios from "axios";
import { EventBus } from "../components/EventBus.js";
import Secretary from "../components/Secretary.vue";
import SideBar from "../components/SideBar.vue";
export default {
  name: "SecretaryPage",
  components: {
    Secretary,
    SideBar,
  },
  data() {
    return {
      secretary: [], //学生信息的json数组
      dialogFormVisible: false,
      form: {
        secretaryNo: "",
        name: "",
        gender: "",
        age: "",
        hireDate: "",
        duty: "",
      },
      formLabelWidth: "120px",
    };
  },
  methods: {
    getSecretary() {
      axios({
        url: "http://localhost:8080/findSecretary",
        method: "GET",
      }).then((res) => {
        console.log(res.data);
        const originalDate = new Date(this.secretary.hireDate);
        this.secretary.hireDate = originalDate;
        this.secretary = res.data;
      });
    },

    addSecretary() {
      axios({
        url: "http://localhost:8080/insertSecretary",
        method: "post",
        data: this.form,
      }). then(() => {
      this.dialogFormVisible = false;
      this.getSecretary();
      this.resetForm(); // 重置表单
    })
    .catch(error => {
      // 获取错误信息并显示
      // const errorMessage = error.response.data.message;
      // this.$message.error(errorMessage);
      // const errorMessage = `Status: ${error.response.data.status}, Error: ${error.response.data.error}`;
      this.$message.error(error);
    });
    },

    resetForm() {
    this.form = { // 重置表单数据
      secretaryNo: "",
      name: "",
      gender: "",
      age: "",
      hireDate: "",
      duty: "",
    };
  },
  },
  created() {
    EventBus.$on("secretary-updated", (data) => {
      this.secretary = data;
    });
  },
};
</script>
  
  <style>
#app {
  display: flex;
  justify-content: space-between;
}

.sidebar {
  width: 200px;
}

.content {
  flex: 1;
  height: 100vh;
  width: 80vw;
}

.table {
  width: 100%;
}

.el-menu {
  height: 100%;
}

.el-menu-item-group,
.el-submenu__title {
  display: flex;
  align-items: center;
}
.table-caption {
  text-align: center;
  font-size: 1.2em;
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  align-content: center;
}
</style>